<template>
  <Page
    v-model:isRefresh="isRefresh"
    title="Launchpad"
    hideHeader
    :openRefresh="true"
    :onRefresh="onRefresh"
    :onClickRight="toSubscriptionHistory"
    :onClickLeft="onClickLeft"
    bodyClass="bg-d"
  >
    <template #right>
      <IconSvg name="icon-a-zu4212" class="font-36" />
    </template>
    <Row class="h-324 w-100b bg-primary o-hidden" x="c">
      <van-image v-if="languageStore.locale == LANGCN" class="h-324 w-100b" :src="headerzhImage" none />
      <van-image v-if="languageStore.locale == LANGEN" class="h-324 w-100b" :src="headerusImage" none />
      <van-image v-if="languageStore.locale == LANGTW" class="h-324 w-100b" :src="headertwImage" none />
    </Row>
    <!-- <Row class="w-90b opacity-90 position-r top-100 z-11">
      <div class="f-60b bg-error h-100b font-24">X</div>
      <div class="f-30b  bg-info">1</div>
    </Row> -->

    <Bitabs
      :model-value="tabValue"
      :options="options"
      :labelClass="'font-32 font-w-400'"
      :labelClassAc="'font-w-400 bold'"
      :rowSpace="30"
      :itemClass="'h-92'"
      :lineClass="'h-4 w-28'"
      class="p-l-30 bg-white"
      @tabChange="(key)=>tabValue = key"
    />

    <Column class="w-100b p-lr-30 p-b-30">
      <Column v-if="tabValue==='inProgress'" class="w-100b m-t-30">
        <van-skeleton v-if="loading" class="f-column w-100b h-673 p-20 bg-white radius-8 o-hidden f-sb">
          <template #template>
            <Column>
              <van-skeleton-image class="w-100b h-340 m-b-10 radius-8" />
              <van-skeleton-paragraph round row-width="80%" />
              <van-skeleton-paragraph round row-width="60%" />
              <van-skeleton-paragraph round row-width="30%" />
            </Column>
            <van-skeleton-paragraph class="h-80 radius-8" row-width="100%" />
          </template>
        </van-skeleton>
        <ProjectList v-if="list?.length > 0" :list="list" @refresh="pGetData" />
        <Column v-if="list?.length == 0" class="w-100b h-480 bg-white radius-8" x="c" y="c">
          <img class="w-128 h-124" src="@/image/no-list.webp">
          <span class="c-info label m-t-40">{{ $t('ieo.noProjectYet') }}</span>
        </Column>
      </Column>

      <Column v-if="tabValue==='completed'" class="w-100b m-t-30">
        <van-skeleton v-if="loading" class="f-column w-100b h-673 p-20 bg-white radius-8 o-hidden f-sb">
          <template #template>
            <Column>
              <van-skeleton-image class="w-100b h-340 m-b-10 radius-8" />
              <van-skeleton-paragraph round row-width="80%" />
              <van-skeleton-paragraph round row-width="60%" />
              <van-skeleton-paragraph round row-width="30%" />
            </Column>
            <van-skeleton-paragraph class="h-80 radius-8" row-width="100%" />
          </template>
        </van-skeleton>
        <ProjectList v-if="finishList?.length > 0" class="m-b-100" :list="finishList" />
        <Column v-if="finishList?.length == 0" class="w-100b h-480 bg-white radius-8 m-b-100" x="c" y="c">
          <img class="w-128 h-124" src="@/image/no-list.webp">
          <span class="c-info label m-t-40">{{ $t('ieo.noProjectYet') }}</span>
        </Column>
      </Column>
    </Column>
  </Page>
</template>
<script setup>
import { ProjectList } from '@/view/ieo/components/index'
import headerzhImage from '@/image/ieo/header-zh_cn.webp'
import headertwImage from '@/image/ieo/header-zh_tw.webp'
import headerusImage from '@/image/ieo/header-us_en.webp'

const route = useRoute()
const languageStore = useLanguageStore()
let loading = $ref(false)
let isRefresh = $ref(false)

let list = $ref()
let finishList = $ref()

let tabValue = $ref('inProgress')
const options = $computed(() => {
  return [
    {
      label: $t('public.inProgress'),
      key: 'inProgress',
    },
    {
      label: $t('form.completed'),
      key: 'completed',
    }
  ]
})

const getData = async () => {
  try {
    loading = true
    // await delay(40000)
    const data = await ieoApi.list({})

    list = data.filter(e => [0, 1, 2].includes(e.projectProcess))
    finishList = data.filter(e => [3, 4, 5].includes(e.projectProcess))
    if (isEmpty(list)) {
      tabValue = 'completed'
    }
  } finally {
    loading = false
  }
}

const pGetData = async () => {
  // setTimeout(() => {
  getData()
  // }, 2500)
}

const onRefresh = () => {
  console.log('-onRefresh-')
  try {
    isRefresh = true
    getData()
  } finally {
    isRefresh = false
  }
}

const toSubscriptionHistory = () => {
  console.log('-toSubscriptionHistory')
  const query = reserveUrlQuery(route)
  router.push({
    path: '/ieo/subscriptionHistory',
    query: query,
  })
}

const onClickLeft = () => {
  // router.go(-1)
  winMes.postParent('toHome')
}
winMes.postParent('hiddenNaviBar')

onBeforeMount(() => {
  getData()
  inModule(['/ieo/projectInfo', '/ieo/subscriptionHistory'])
})

</script>
<style lang="scss">
.Ieo{
  .slidePrev{
    position: absolute;
    top: 50%;
    left: -60px;
  }
  .slideNext{
    position: absolute;
    top: 50%;
    right: -60px;
  }
}
</style>
