<template>
  <Column class="w-100b" x="c">
    <ProjectItem
      v-for="(item, i) in list"
      :id="item.id"
      :key="item.id"
      :class="i ? 'm-t-40' : ''"
      :status="item.projectProcess"
      :name="item.name"
      :fullName="item.fullName"
      :imgUrl="item.imgUrl"
      :model="item.ieoModel"
      :synopsis="getSynopsis(item)"
      :totalCopies="item.totalCopies"
      :preAmount="item.preAmount"
      :preAmountMoney="item.preAmountMoney"
      :startTime="item.ieoActivityStartTime"
      :endTime="item.ieoSubscribeEndTime"
      @refresh="refresh"
    />
  </Column>
</template>
<script setup>
import { Swiper, SwiperSlide, useSwiper } from 'swiper/vue'
import { Navigation } from 'swiper'
import 'swiper/css'
import 'swiper/css/navigation'
import { ProjectItem } from '@/view/ieo/components/index'

const emit = defineEmits()
const languageStore = useLanguageStore()
const props = defineProps({
  list: {},
})

const loading = $ref(false)

const getSynopsis = (item) => {
  return item?.languageJson?.find((obj) => obj.language === languageStore.locale)?.introduction
}

const refresh = () => {
  emit('refresh')
}

</script>
