
<template>
  <Column class="w-100b position-r pointer radius-8 bg-white o-hidden" :style="{'box-shadow': '0px 0px 20px 1px rgba(38,73,239,0.08)'}" @click="toInfo">
    <div class="position-a-tr z-1 p-lr-20 p-tb-4 c-white label radius-bl-14" :class="statusTabClass[status]">{{ statusLabel }}</div>
    <div class="position-r w-100b h-360">
      <Row class="w-100b  h-360 o-hidden" y="c" x="c">
        <van-image class="radius-8" :src="imgUrl" none />
      </Row>
      <!-- <Row class="position-a-br p-10"> -->
      <Row v-if="!countdown.exceed" class="position-a-br p-20">
        <span v-if="status == 0" class="h-38 f-center label-sm bg-white-80 p-lr-10 radius-4">{{ $t('ieo.subStatusTime1') }}</span>
        <Row v-if="status == 0">
          <span class="w-38 h-38 f-center radius-4 label-sm c-primary bg-white-80 m-l-14 bold">{{ countdown.day }}</span>
          <span class="w-38 h-38 f-center radius-4 label-sm c-primary bg-white-80 m-l-14 bold">{{ countdown.hours }}</span>
          <span class="w-38 h-38 f-center radius-4 label-sm c-primary bg-white-80 m-l-14 bold">{{ countdown.minutes }}</span>
          <span class="w-38 h-38 f-center radius-4 label-sm c-primary bg-white-80 m-l-14 bold">{{ countdown.seconds }}</span>
        </Row>
      </Row>
    </div>
    <Column class="w-100b p-lr-20 p-b-30">
      <Row class="m-t-20 m-b-24">
        <span class="label-lg bold m-r-10">{{ name }}</span>
        <span class="label-sm c-info m-r-8">{{ fullName }}</span>
        <span class="c-primary label-sm bg-primary-10 p-lr-10 p-tb-2 radius-18">{{ modelLabel }}</span>
      </Row>
      <div class="label-sm c-info m-b-24 lh-36 text-left ellipsis-2">{{ synopsis }}</div>
      <Row class="w-100b m-b-24 label-sm" x="sb">
        <Row>
          <span>{{ startTime.format('YYYY.MM.DD HH:mm') }}</span>
          <span class="m-lr-4">-</span>
          <span>{{ endTime.format('MM.DD HH:mm') }}</span>
          <!-- <span class="m-l-4">(UTC+8)</span> -->
        </Row>
      </Row>
      <van-button class="label w-100b h-80 bold" type="primary">{{ [3,4].includes(status)? $t('form.clickToView') : $t('form.joinNow') }}</van-button>
    </Column>
  </Column>
</template>
<script setup>
import dayjs from 'dayjs'

const emit = defineEmits()
const route = useRoute()
const props = defineProps({
  id: {},
  status: {},
  imgUrl: {},
  name: {},
  fullName: {},
  model: {},
  synopsis: {},
  totalCopies: {},
  preAmount: {},
  preAmountMoney: {},
  startTime: {},
  endTime: {},
})

const statusLabelMap = $computed(() => ({
  0: $t('public.preheating'),
  1: $t('public.inProgress'),
  2: $t('public.subscription'),
  3: $t('public.toBeIssued'),
  4: $t('public.finished'),
  5: $t('public.unlocking'),
}))

const modelLabelMap = $computed(() => ({
  0: $t('ieo.starterMode'),
  1: $t('ieo.discountMode'),
}))

const statusTabClass = {
  0: 'bg-warning',
  1: 'bg-success',
  2: 'bg-primary',
  3: 'bg-error',
  4: 'bg-info',
  5: 'bg-primary',
}

const startTime = $computed(() => dayjs(Number(props.startTime)))
const endTime = $computed(() => dayjs(Number(props.endTime)))
const time = $computed(() => props.status == 0 ? startTime : endTime)

const { countdown } = useCountdown(() => time)

const statusLabel = $computed(() => statusLabelMap[props.status])
const modelLabel = $computed(() => modelLabelMap[props.model])

const toInfo = () => {
  const query = { id: props.id, ...reserveUrlQuery(route) }
  router.push({
    path: '/ieo/projectInfo',
    query: query,
  })
}

watch(() => countdown.exceed, () => {
  emit('refresh')
})

</script>
<style lang="scss">
.IeoProjectItem{

}
</style>
